// Typography web

// Family: Biysk
//- Regular
@font-face {
    font-family: 'Biysk';
    src: url('../fonts/Biysk-Regular.woff2') format('woff2'),
    url('../fonts/Biysk-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

// Family: Kanit
//- Regular
@font-face {
    font-family: 'Kanit';
    src: url('../fonts/Kanit-Regular.woff2') format('woff2'),
    url('../fonts/Kanit-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

// Placeholders : Biysk
%biysk {
    font-family: $font-biysk;
    font-weight: normal;
    font-style: normal;
}

// Placeholders : Kanit
%kanit {
    font-family: $font-kanit;
    font-weight: normal;
    font-style: normal;
}