// Telechargement
.dwnl {
    margin: 0 20px;
}
.dwnl__listing {
    max-height: 418px;
    overflow-y: scroll;
}
.dwnl__item {
    display: flex;
    margin: 0 0 20px;
    align-items: center;
    &:last-child {
        margin-bottom: 0;
    }
}
.dwnl__title {
    color: $color-blue;
    text-decoration: none;
    padding-left: 35px;
    background: url("../images/ico-camera.svg") no-repeat 0 0;
    background-size: 24px auto;
    flex-grow: 1;
}
.status {
    display: block;
    min-width: 30px;
    height: 30px;
    &.initial {
        background: url("../images/ico-arrow-left.svg") no-repeat center center;
        transform: rotate(-90deg);
    }
    &.isLoaded {
        background: url("../images/ico-check.svg") no-repeat center center;
    }
}