.btn {
    border-radius: 30px;
    height: 48px;
    border: 0;
    width: 100%;
    cursor: pointer;
    font-weight: bold;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    &.btn--connexion {
        color: $color-white;
        background-color: $color-dark-blue;
    }
}
.btn--inactive {
    background-color: $color-light-blue;
    color: $color-white;
}
.btn--active {
    background-color: $color-green;
    color: $color-white;
}
.btn--alert {
    background-color: $color-red;
    color: $color-white;
}

.btnDel {
    min-width: 40px;
    width: 40px;
    height: 40px;
    background: $color-grey url("../images/ico-delete.svg") no-repeat center center;
    background-size: 50% auto;
    border: 0;
    border-radius: 50%;
    overflow: hidden;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-indent: 50px;
    white-space: nowrap;
    cursor: pointer;
}
.barBtn {
    margin: 0 48px;
    position: absolute;
    bottom: 29px;
    left: 0;
    right: 0;
}