// Listing
.member__listing {
    margin: 0 20px;
    max-height: 300px;
    overflow-y: scroll;
    padding: 10px 0;
    box-sizing: border-box;
}
.member__item {
    display: flex;
    margin: 0 0 20px;
    &:last-child {
        margin-bottom: 0;
    }
    .materialField {
        margin: 0 0 0 10px;
    }
}
.member__link {
    flex-grow: 1;
    text-decoration: none;
    background: url("../images/ico-profile.svg") no-repeat 0 0;
    background-size: 20px auto;
    padding: 0 0 0 30px;
    line-height: 2rem;
    color: $color-blue2;
}