$grid-gutter: 5px;
.row {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-right: -$grid-gutter;
    margin-left: -$grid-gutter;
    clear: both;
    &.start {
        justify-content: flex-start;
    }
    &.end {
        justify-content: flex-end;
    }
    &.center {
        justify-content: center;
    }
    &.top {
        align-items: flex-start;
    }
    &.middle {
        align-items: center;
    }
    &.bottom {
        align-items: flex-end;
    }
    &.around {
        justify-content: space-around;
    }
    &.between {
        justify-content: space-between;
    }
    &.reverse {
        flex-direction: row-reverse;
    }
    [class*="col-"] {
        box-sizing: border-box;
        flex: 0 0 auto;
        padding-right: $grid-gutter;
        padding-left: $grid-gutter;
    }
    .col-auto {
        flex-grow: 1;
        flex-basis: 0;
        max-width: 100%;
    }
    .col-12 {
        flex-basis: 100%;
        max-width: 100%;
    }
    .col-11 {
        flex-basis: 100% / 12 * 11;
        max-width: 100% / 12 * 11;
    }
    .col-10 {
        flex-basis: 100% / 12 * 10;
        max-width: 100% / 12 * 10;
    }
    .col-9 {
        flex-basis: 100% / 12 * 9;
        max-width: 100% / 12 * 9;
    }
    .col-8 {
        flex-basis: 100% / 12 * 8;
        max-width: 100% / 12 * 8;
    }
    .col-7 {
        flex-basis: 100% / 12 * 7;
        max-width: 100% / 12 * 7;
    }
    .col-6 {
        flex-basis: 100% / 12 * 6;
        max-width: 100% / 12 * 6;
    }
    .col-5 {
        flex-basis: 100% / 12 * 5;
        max-width: 100% / 12 * 5;
    }
    .col-4 {
        flex-basis: 100% / 12 * 4;
        max-width: 100% / 12 * 4;
    }
    .col-3 {
        flex-basis: 100% / 12 * 3;
        max-width: 100% / 12 * 3;
    }
    .col-2 {
        flex-basis: 100% / 12 * 2;
        max-width: 100% / 12 * 2;
    }
    .col-1 {
        flex-basis: 100% / 12 * 1;
        max-width: 100% / 12 * 1;
    }
    // Offset
    .col-offset-11 {
        margin-left: 100% / 12 * 11;
    }
    .col-offset-10 {
        margin-left: 100% / 12 * 10;
    }
    .col-offset-9 {
        margin-left: 100% / 12 * 9;
    }
    .col-offset-8 {
        margin-left: 100% / 12 * 8;
    }
    .col-offset-7 {
        margin-left: 100% / 12 * 7;
    }
    .col-offset-6 {
        margin-left: 100% / 12 * 6;
    }
    .col-offset-5 {
        margin-left: 100% / 12 * 5;
    }
    .col-offset-4 {
        margin-left: 100% / 12 * 4;
    }
    .col-offset-3 {
        margin-left: 100% / 12 * 3;
    }
    .col-offset-2 {
        margin-left: 100% / 12 * 2;
    }
    .col-offset-1 {
        margin-left: 100% / 12 * 1;
    }
}

