.form {
    width: 100%;
    &.connexion {
        input {
            background: $color-white;
            width: 100%;
            height: 48px;
            border: 0;
            box-sizing: border-box;
            padding: 0 20px;
            color: $color-almost-black;
            border-radius: 30px;
            margin: 0 0 20px;
            &::placeholder {
                color: $color-blue;
                opacity: 1;
            }
            &:-ms-input-placeholder {
                color: $color-blue;
                opacity: 1;
            }
            &::-ms-input-placeholder {
                color: $color-blue;
                opacity: 1;
            }
            &:focus {
                outline: 1px dotted;
            }
        }
    }

    .invalid-text {
        text-align: center;
        color: $color-form-invalid;
        font-weight: bold;
        margin: 10px 0;
    }

    .form-control.is-invalid {
        &::placeholder {
            color: $color-form-invalid;
        }
        &:-ms-input-placeholder {
            color: $color-form-invalid;            
        }
        &::-ms-input-placeholder {
            color: $color-form-invalid;
        }
    }
}

// Comptes
.list-account {
    max-height: 228px;
    overflow-y: scroll;
    margin: 0 0 20px;
    .btnDel {
        margin: 0 0 0 10px;
    }
}
.list-account__entry {
    display: flex;
    align-items: center;
    margin: 0 0 20px;
    &:last-child {
        margin-bottom: 0;
    }
}
.field-like {
    background: $color-white;
    width: 100%;
    height: 48px;
    border: 0;
    box-sizing: border-box;
    padding: 8px 20px;
    color: $color-blue;
    border-radius: 30px;
    .first {
        font-weight: bold;
    }
}

// Recherche
.search {
    margin: 0 48px 20px 48px;
    display: flex;
    align-items: center;
    border-radius: 60px;
    border: 1px solid $color-blue;
    position: relative;
    height: 48px;
}
.search__field {
    margin: 0;
    padding: 0;
    border: 0;
    position: absolute;
    left: 15px;
    top: 50%;
    transform: translateY(-50%);
    height: 40px;
    width: calc(100% - (48px + 15px));
    &::placeholder {
        color: $color-blue;
        opacity: 1;
    }
    &:-ms-input-placeholder {
        color: $color-blue;
        opacity: 1;
    }
    &::-ms-input-placeholder {
        color: $color-blue;
        opacity: 1;
    }
    &:focus {
        outline: 1px dotted;
    }
}
.search__btn {
    height: 48px;
    width: 40px;
    border: 0;
    background: url("../images/ico-loupe.svg") no-repeat 0 center;
    background-size: 50% auto;
    position: absolute;
    right: 0;
    top: 0;
    padding: 0;
    overflow: hidden;
    text-indent: 100px;
    white-space: nowrap;
    cursor: pointer;
}

// Variables radioMaterial
// - Colors vars
$color-material: $color-green;
// - Measures
$width: 30px;
$height: 30px;
// - Animation
$time-anim: .28s;

// Field
.materialField {
    position: relative;
    width: $width;
    height: $height;
}

// Input radio
.radioMaterial {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    &:not(:checked),
    &:checked {
        position: absolute;
        opacity: 0;
        pointer-events: none;
    }
}

// Label
.labelRadioMaterial {
    width: $width;
    height: $height;
    overflow: hidden;
    text-indent: 100px;
    white-space: nowrap;
    // Default
    .radioMaterial:not(:checked) + &,
    .radioMaterial:checked + & {
        position: relative;
        cursor: pointer;
        display: inline-block;
        height: $height;
        width: $width;
        user-select: none;
        &:before,
        &:after {
            border: 1px solid $color-material;
            border-radius: 50%;
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: $width;
            height: $height;
            z-index: 0;
            -webkit-transition: $time-anim ease;
            transition: $time-anim ease;
            box-sizing: border-box;
        }
    }
    // Not checked
    .radioMaterial:not(:checked) + &:after {
        transform: scale(0);
    }
    // Checked
    .radioMaterial:checked + & {
        &:after {
            background-color: $color-material;
        }
    }
}