// Bienvenue
.welcome {
    .main {
        height: calc(100vh - 60px);
    }
    .underNav {
        display: none;
    }
}
.bienvenue {
    height: calc(100vh - 60px);
    box-sizing: border-box;
    padding: 15px 0 0;
    background: url("../images/bg-city.png") no-repeat center 80px;
    background-size: contain;
    position: relative;
    &:after {
        content: '';
        height: 90px;
        width: 100vw;
        position: absolute;
        bottom: 20px;
        left: 0;
        right: 0;
        background: url("../images/bg-interior.png") no-repeat center bottom;
        background-size: contain;
    }
}
.bienvenue__intro {
    text-align: center;
    p {
        margin: 0 0 10px;
    }
}
.bienvenue__list {
    margin: 75px 0 0;
}
.bienvenue__item {
    text-align: center;
    margin: 20px 0;
    a {
        display: inline-block;
        width: 160px;
        height: 160px;
        img {
            max-width: 100%;
            height: auto;
        }
    }
}