// Global styles
html {
    font-size: 62.5%;
}
body {
    color: $color-global-txt;
    font-size: 1.4rem;
    height: 100vh;
    font-family: Arial, Helvetica, sans-serif;
    overflow-x: hidden;
    box-sizing: border-box;
}

audio, canvas, iframe, img, svg, video {
    vertical-align: middle;
}

p {
    margin: 0;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

a {
    color: $color-global-link;
}

.container {
    box-sizing: border-box;
    width: calc(100% - #{$container-padding * 2});
    margin: 0 auto;
}

.grid-align-left {
    position: absolute;
    left: 0;
}
.grid-align-right {
    position: absolute;
    right: 0;
}

.grid-flex {
    display: flex;
}

.hide {
    visibility: hidden;
}

.dnone {
    display: none;
}

.logo {
    color: $color-blue;
    font-size: 4rem;
    @extend %biysk;
    a {
        text-decoration: none;
        color: $color-blue;
        display: block;
    }
}
.menu-link {
    text-decoration: none;
    color: $color-red;
    font-size: 2rem;
    @extend %biysk;
}
.underNav {
    margin: 0 13px;
    text-align: center;
    position: relative;
}
.back-link {
    background: url("../images/ico-arrow-left.svg") no-repeat 0 center;
    background-size: 16px auto;
    display: block;
    width: 40px;
    height: 40px;
    overflow: hidden;
    text-indent: 100px;
    white-space: nowrap;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
}
.parcours-title {
    line-height: 40px;
    font-size: 2rem;
    @extend %kanit;
    a {
        text-decoration: none;
        display: block;
    }
}
.intro {
    background: $color-grey2;
    padding: 10px;
    margin: 0 0 20px;
    text-align: center;
    p {
       line-height: 2rem;
       margin: 0 0 5px;
       &:last-child {
           margin-bottom: 0;
       }
    }
}
.spotlight {
    text-align: center;
    color: $color-blue2;
    line-height: 2rem;
    span {
        display: inline-block;
        width: 30px;
        height: 20px;
        background: url("../images/ico-profile.svg") no-repeat 0 0;
        vertical-align: middle;
    }
}

// Nav
.header nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 13px;
    height: 60px;
    .menu-link,
    .logo,
    .synchro {
        margin: 0;
    }
    .synchro {
        background: url("../images/ico-synchro.svg") no-repeat center center;
        background-size: 100% auto;
        width: 32px;
        height: 20px;
        overflow: hidden;
        text-indent: 100px;
        white-space: nowrap;
        display: block;
    }
}

.main {
    height: calc(100vh - 100px);
    position: relative;
}

// Layout Connexion
.connex {
    background: rgb(6,190,225);
    background: linear-gradient(180deg, rgba(6,190,225,1) 0%, rgba(6,190,225,0.4) 70%, rgba(6,190,225,0.14) 100%);
    background-repeat: no-repeat;
    .header {
        padding: 30px 0 40px;
    }
    .wrapper {
        padding: 0 13%;
    }
    .logo {
        font-size: 6rem;
        line-height: 4.5rem;
        margin: 0;
        color: $color-white;
        text-align: center;
    }
    .avatar {
        display: block;
        margin: 0 auto 39px auto;
        width: 60px;
        height: 60px;
        border-radius: 50%;
    }
    .text-helper {
        text-align: center;
        color: $color-white;
        font-weight: bold;
        margin: 0 0 35px;
    }
    .form.connexion {
        margin: 0 0 20px;
    }
}
.connex-link {
    display: block;
    text-align: center;
    text-decoration: underline;
    font-weight: bold;
    color: $color-dark-blue;
}