// Progression
.progression {
    margin: 0 20px;
    .spotlight {
        margin: 0 0 45px;
    }
}
.progression__jeton {
    margin: 0 0 20px;
    span {
        font-weight: bold;
    }
}
.progression__title {
    margin: 0 0 24px;
}
.progression__list {
    margin: 0 0 30px;
    max-height: 179px;
    overflow-y: scroll;
}
.progression__item {
    display: flex;
    margin: 0 0 30px;
    align-items: center;
    .module {
        flex-grow: 1;
        color: $color-blue;
        padding-left: 35px;
        background: url("../images/ico-camera.svg") no-repeat 0 0;
        background-size: 24px auto;
    }
}
.pif {
    color: $color-red;
}
.check {
    border: 1px solid $color-black;
    padding: 2px 6px;
    &.done {
        border-color: $color-green;
        color: $color-green;
    }
    &.undone {
        border-color: $color-orange;
        color: $color-orange;
    }
}