// Menu
.menu__listing {
    max-height: calc(100vh - 100px);
    overflow-y: scroll;
}
.menu__item {
    text-align: center;
    margin: 0 0 15px;
    background-repeat: no-repeat;
    background-position: center 30px;
    &:nth-child(2n+1) { // ODD
        background-color: $color-grey2;
    }
    &:nth-child(2n) { // EVEN
        background-color: $color-white;
    }
    &:last-child {
        margin-bottom: 0;
    }
    // Telecharger
    &.item--dwl {
        background-image: url("../images/ico-camera.svg");
        background-size: 50px auto;
    }
    // Selectionner
    &.item--select {
        background-image: url("../images/ico-people.svg");
    }
    // Consulter
    &.item--consult {
        background-image: url("../images/ico-rapport.svg");
    }

    a {
        display: block;
        text-decoration: none;
        height: 160px;
        box-sizing: border-box;
        font-size: 1.8rem;
        line-height: 2.4rem;
        color: $color-blue2;
        padding: 85px 60px 0 60px;
    }
}